h2 {
  font-family: 'Calibre', 'San Francisco', 'SF Pro Text', -apple-system,
    system-ui, sans-serif;
}

.otherContent {
  font-size: 1.3rem;
  line-height: 1.2em;
  margin: 10px 2px 10px 2px;
  font-weight: normal;
}

.aboutme {
  display: flex;
  justify-content: left;
  flex-direction: row;
  align-items: center;
  min-height: 100vh;
  line-height: 1.3em;
}

.mailButton {
  @include themed() {
    color: theme('color');
    border: 1px solid theme('hireMeColor');
  }

  font-size: 1.05rem;
}

.projects {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: flex-start;
  gap: 12px;

  .btn-link {
    @include themed() {
      color: theme('cardBodyLinkcolor') !important;
    }
  }
}

.centered {
  /* display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center; */
  margin: 20px auto;
  width: 82%;

  /* border: 1px solid skyblue; */
}

.name {
  font-size: larger;
  margin-top: 10px;
  @include themed() {
    color: theme('nameColor');
  }
}

.aboutme .hireme {
  font-style: italic;

  margin-bottom: 14px;
  @include themed() {
    color: theme('hireMeColor');
  }
}

