.scroller {
  scroll-snap-type: y mandatory;
  -webkit-overflow-scrolling: touch;
}

.scroller section {
  scroll-snap-align: start;
}

.blogs {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
  grid-column-gap: 12px;
  grid-row-gap: 12px;
  padding-bottom: 300px;
}

.blogs section {
  height: 100%;
  content-visibility: auto;
}

.card {
  border-radius: 0px !important;

  @include themed() {
    background-color: theme('cardHeaderBackground');
    box-shadow: 1px 1px 4px theme('cardBoxShadowColor');
    border: theme('cardBoxBorderColor');
  }
  text-rendering: optimizeLegibility;
}

.card-header {
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);

  @include themed() {
    background-color: theme('cardHeaderBackground');
  }
}

.card-title {
  font-size: 1.3rem;
  line-height: 1.1em;
  @include themed() {
    color: theme('cardHeaderTitleColor');
  }
}

.card-body {
  line-height: normal;
  font-size: 1.2rem;

  @include themed() {
    background-color: theme('cardBodyBackground');
    color: theme('cardBodycolor') !important;
  }
}

.card-body a {
  font-size: 1rem !important;
  text-decoration: underline;
  @include themed() {
    color: theme('cardBodyLinkcolor') !important;
  }
}

.ghImage {
  height: 32px;
  width: 32px;
  display: inline-block;
  margin-right: 2px;
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .blogs {
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  }

  .centered {
    margin: 35px auto;
  }
}
