@import '_theme.scss';

a {
  font-size: 1.15em !important;
}

.pageLink {
  font-family: 'Fira Code', 'Consolas', 'sans-serif';
  padding-right: 10px !important;
  @include themed() {
    color: theme('pageLinkColor') !important;
  }
}

.pageLink.active {
  @include themed() {
    color: theme('pageLinkActiveColor') !important;
  }
}

nav {
  position: fixed;
  padding: 0 25px;
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1030;
}

.navbar {
  padding: 5px 25px;
  @include themed() {
    box-shadow: theme('navbarBoxShadow');
  }
}

a:hover {
  text-decoration: underline !important;
  background-color: transparent;
  color: #c4cafd !important;
}

.navbar-custom {
  @include themed() {
    background-color: theme('headerfooterBackground');
  }

  .navbar-toggler-icon {
    @include themed() {
      background-image: theme('hamburgerIcon');
    }
  }
}

main > section {
  padding: 50px 0px !important;
}

/* change the brand and text color */
.navbar-custom .navbar-brand,
.navbar-custom .navbar-text {
  color: rgba(255, 255, 255, 0.8);
}
/* change the link color */
.navbar-custom .navbar-nav .nav-link {
  color: rgba(255, 255, 255, 0.5);
}
/* change the color of active or hovered links */
.navbar-custom .nav-item.active .nav-link,
.navbar-custom .nav-item:hover .nav-link {
  @include themed() {
    color: theme('pageLinkActiveColor') !important;
  }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 480px) {
  .main {
    margin-top: 45px;
  }
}
