$themes: (
  dark: (
    backgroundColor: #141a22,
    backgroundGradient: linear-gradient(100deg, #1a2029 0%, #1a202d 100%),
    color: #fefefc,
    headerfooterBackground: #1f293b,
    dropdownBackground: #1a263d,
    dropdownItemColor: #fcfcff,
    pageLinkColor: #89dcfd,
    pageLinkActiveColor: #e1edf1,
    nameColor: #8892b0,
    hireMeColor: cadetblue,
    cardHeaderBackground: #1a202990,
    cardHeaderTitleColor: #ccd6f6,
    cardBodyBackground: #1a202d,
    cardBodycolor: #fffffc,
    cardBodyLinkcolor: #88b6d1,
    cardBoxShadowColor: #000000,
    cardBoxBorderColor: 1px solid rgba(255, 255, 255, 0.1),
    hamburgerIcon:
      url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%28255, 255, 255, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"),
    scrollbarTrack: #3b3b3b,
    scrollbarThumb: #5f646b,
    navbarBoxShadow: 0 1px 2px 0 #37383be7,
  ),
  light: (
    backgroundColor: #f5f9ff,
    // #b0c5e0, bad1ee
    backgroundGradient: linear-gradient(100deg, #fdfeff 0%, #f9f9ff 100%),
    color: #0e0e0c,
    // a7c9f5
    headerfooterBackground: #dcecf5,
    // a7c9f5c5
    dropdownBackground: #dcecf5c5,
    dropdownItemColor: #0e0e0c,
    pageLinkColor: #0c3d6b,
    pageLinkActiveColor: #04101b,
    nameColor: #4d5264,
    hireMeColor: #136c72,
    // afcbecee
    cardHeaderBackground: #add3eef1,
    cardHeaderTitleColor: #141a22,
    // d0e2f7
    cardBodyBackground: #dfedf7,
    cardBodycolor: #1a202d,
    cardBodyLinkcolor: #143566,
    cardBoxShadowColor: #f0e3e3,
    cardBoxBorderColor: 0px solid rgba(255, 255, 255, 0.9),
    hamburgerIcon:
      url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.5%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e"),
    scrollbarTrack: #dfedf7,
    scrollbarThumb: #7796ac,
    navbarBoxShadow: 0 1px 4px 0 #1b1c1fe7,
  ),
);

// https://medium.com/@dmitriy.borodiy/easy-color-theming-with-scss-bc38fd5734d1
@mixin themed($themes: $themes) {
  @each $theme, $map in $themes {
    .theme-#{$theme} & {
      $theme-map: () !global;
      @each $key, $submap in $map {
        $value: map-get(map-get($themes, $theme), '#{$key}');
        $theme-map: map-merge(
          $theme-map,
          (
            $key: $value,
          )
        ) !global;
      }

      @content;
      $theme-map: null !global;
    }
  }
}

@function theme($key) {
  @return map-get($theme-map, $key);
}
