@import '_theme.scss';

.footer {
  @include themed() {
    background-color: theme('headerfooterBackground');
  }
  position: fixed;

  right: 0px;
  left: 0px;
  bottom: 0px;
  width: 100vw;
}

.footer .centered {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  margin: 12px auto;
  padding-left: 0px;
  width: 82%;
}

@media screen and (min-width: 600px) {
  .footer .centered {
    flex-flow: row wrap;
    margin: 16px auto;
  }
}

.dropdown-menu.show,
.dropdown-toggle {
  @include themed() {
    background-color: theme('dropdownBackground') !important;
  }

  border-radius: 0px !important;
  font-size: 0.9em !important;
}

.dropdown {
  .btn-secondary {
    @include themed() {
      color: theme('dropdownItemColor') !important;
    }
  }
}

.dropdown-item {
  @include themed() {
    color: theme('dropdownItemColor') !important;
    background-color: theme('dropdownBackground') !important;
  }

  padding: 0px 0.75rem;

  .themeItem {
    padding: 0.25rem 1.6rem;
  }
}

.dropdown-item:hover {
  .themeItem {
    @include themed() {
      color: theme('dropdownItemColor') !important;
    }
    background-color: #7393cf !important;
  }
}
.dropdown-item:focus {
  padding: 2px 0.75rem;
}

.dropdown-item.active,
.dropdown-item:active {
  @include themed() {
    background-color: theme('headerfooterBackground');
  }

  .themeItem {
    @include themed() {
      border: 1px solid theme('color');
      background-color: darken(
        $color: theme('dropdownBackground'),
        $amount: 10%
      );
    }
  }
}
